.form__label {
    font-size: 25px;
    padding-right: 15px;
    margin-bottom: 50px;
    margin-left: 30px;

    @media (max-width: 768px) {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
}

.form__input {
    height: 25px;
    color: black;
    font-size: 18px;
    background-color: #03abc2;
    border-radius: 3px;
    border: 3px solid #a8cb50;
    margin-bottom: 50px;
    padding-left: 5px;

    &.species {
        height: 35px;
    }

    @media (max-width: 768px) {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
}