@import "./CharacterList.scss";
@import "./CharacterDetail.scss";
@import "./Character.scss";
@import "./FilterN.scss";
/*@import "../components/Font.scss";*/

.App {
  width: 100%;
  text-align: center;
  background-color: #282c34;
  letter-spacing: 3px;
}

.logo__container {
  justify-content: center;
  margin: auto;
 
  

  @media (max-width: 768px) {
    width: 300px;
  }

  padding: 40px 10px;
}

.logo {
  width: 100%;
  height: 100%;
}

.backToHomepage {
  display: block;
  color: #a8cb50;
  margin: auto;
}

.unfinded__container {
  height: 66.3vh;
}

.unfinded__text {
  font-size: 30px;
  padding: 60px;
  text-transform: uppercase;
}

.unfinded__homepage {
  display: block;
  color: #a8cb50;
  margin: auto;
}

a {
  text-decoration: none;
}

.form {
  @media (max-width: 768px) {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}